.mobile-nav {
  &__container {
    width: 266px;
  }
  &__list {
    padding: 20px var(--container-padding-x);
    &:not(:last-child) {
      border-bottom: 1px solid var(--color-1);
    }
  }
  &__item {
    display: flex;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__icon-link {
    font-size: 18px;
    line-height: 26px;
    --icon-size: 20px;
  }
}
  